<template>
  <div style="max-width: 100%;box-sizing: border-box" class="problemFeedback">
    <myHeader
      :isBack="true"
      backPath="/business"
      title="通知列表"
    >
    </myHeader>
    <div class="seat-head"></div>
    <div class="list-container" v-if="show">
      <div  v-for="question in questionList">
        <div class="list-item">
          <div class="left"  @click="gotoFeedbackDetail(question.rcordId)">
            <div class="record-title">{{question.number}}</div>
            <div class="record-time">{{question.bizDate}}</div>
          </div>
          <div class="right">
            <span class="record-detail"></span>
          </div>
        </div>
      </div>
    </div>
    <NoRecord v-else></NoRecord>
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import RecordList from "../../../components/RecordList";
  import { Toast } from 'mint-ui';
  import NoRecord from '@/components/NoRecord';
  import { Indicator,MessageBox  } from 'mint-ui';
  import {getSalesList} from '@/api/business'

  export default {
    name: 'noticeList',
    data() {
      return {
        myCustomStyle: {
          background: 'rgba(255,255,255,1)',
          color: '#FF6F1F',
          position:'fixed',
          bottom:'0',
          width:'100%',
          borderRadius:'0',
          margin:'0',
          borderTopWidth:'1px',
          borderTopColor:'#ddd',
          borderTopStyle:'solid'
        },
        show:'true',
        myBtnText: '提问',
        questionList:[]
      }
    },
    components: {
      RecordList,
      myHeader,
      NoRecord
    },
    created(){
      this._getQuessionList();
    },
    methods: {
      gotoFeedbackDetail(recordId) {
        // this.$router.push({name: 'salesNotice', params: {recordId}})
        this.$router.push(`/salesNotice?rcordId=${recordId}`);
      },
      _getQuessionList(){
        Indicator.open();
        setTimeout(()=>{
          Indicator.close();
        },2000);
        getSalesList().then((res)=>{
          if(res.data.list.length > 0){
            this.questionList = res.data.list;
            Indicator.close();
          }else{
            this.show =false;
          }
        }).catch((err)=>{
          this.show =false;
          Indicator.close();
          Toast({
            message: '获取信息失败',
            duration: 1000
          });
        })
      }
    }
  }
</script>

<style lang="scss">
  .problemFeedback {
    min-height: 100vh;
    background-color: #F8F8F8;

    .head {
      background-color: #FFF;
    }

    .list-container {
      margin-top: .1rem;
    }

    .list-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 0.1rem;
      font-size: 0.16rem;
      background-color: #fff;
      height: 0.69rem;
      border-bottom: 1px solid #dddddd;
      .left{
        width: 76%;
        overflow: hidden;
        .record-title{
          white-space: nowrap;
          overflow: hidden;
          text-overflow:ellipsis;
        }
        .record-time {
          font-size: 0.14rem;
          color: #8b8b8d;
          margin-top: 0.07rem;
        }
      }

      .right {
        .record-state {
          font-size: 0.12rem;
          border:1px solid #dddddd;
          color: #000;
          padding:.02rem .04rem;
        }
        .record-detail {
          display: inline-block;
          margin-left: 0.19rem;
          width: 0.1rem;
          height: 0.1rem;
          background: url("~@/assets/icon-next.png") no-repeat;
          background-size: 100%;
          transform: rotate(-90deg);
          opacity: 0.4;
        }
      }
    }
  }
</style>



